/* eslint-disable */
    import ClassicEditor from '@ckeditor/ckeditor5-editor-classic/src/classiceditor';

    import CodeBlock from '@ckeditor/ckeditor5-code-block/src/codeblock';
    import EssentialsPlugin from '@ckeditor/ckeditor5-essentials/src/essentials';
    import BoldPlugin from '@ckeditor/ckeditor5-basic-styles/src/bold';
    import ItalicPlugin from '@ckeditor/ckeditor5-basic-styles/src/italic';
    import LinkPlugin from '@ckeditor/ckeditor5-link/src/link';
    import ParagraphPlugin from '@ckeditor/ckeditor5-paragraph/src/paragraph';

export default {

    name: 'Edit',
    data() {
        return {
          content: null,
          title: '',
          notecontent: '',
          message: '',
          langtag: '',
          id: this.$route.params.id,
          dense: '',
          editorConfig: {
              plugins: [
                  EssentialsPlugin,
                  BoldPlugin,
                  ItalicPlugin,
                  LinkPlugin,
                  ParagraphPlugin,
                  CodeBlock
              ],
              toolbar: {
                  items: [
                      'bold',
                      'italic',
                      'link',
                      'undo',
                      'redo',
                      'codeBlock'
                  ]
              }

          },
          editor: ClassicEditor
        }
    },
    async created(){
          if (!this.$store.getters.isLoggedIn) {
      this.$router.push('/login');
    }
    },
        mounted(){
        this.$http.get('/notes/'+this.id,{headers: {Authorization: 'Bearer ' + this.$store.state.token}}).then(res => {
            this.title = res.data.title
            this.langtag = res.data.langtag
            this.content = res.data.content

        })

        this.name = localStorage.getItem('user')


    },
    methods: {
        edit() {
            const editNote = [
                { propName:'title', value: this.title,},
                { propName:'content', value: this.content,},
                { propName:'langtag', value: this.langtag,},
            ]
            if (editNote.langtag === ''){
                editNote.langtag = undefined
            }
           this.$http.patch('/notes/'+this.id, editNote, {headers: {Authorization: 'Bearer ' + this.$store.state.token}}).then(res =>{
               this.message = res.data.message
               this.$router.push('/');
           }
           )
           .catch(err => {
               if (this.message == 'Error: Request failed with status code 401') {
                   this.$router.push('/login')
               }
           })
        }

    }
}
